@font-face {
  font-family: "swiper-icons";
  src: url("data:application/font-woff;charset=utf-8;base64, ") format("woff");
  font-weight: 400;
  font-style: normal;
}
:root {
  --swiper-theme-color: #007aff;
}

.swiper {
  margin-left: auto;
  margin-right: auto;
  position: relative;
  overflow: hidden;
  list-style: none;
  padding: 0;
  /* Fix of Webkit flickering */
  z-index: 1;
}

.swiper-vertical > .swiper-wrapper {
  flex-direction: column;
}

.swiper-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 1;
  display: flex;
  transition-property: transform;
  box-sizing: content-box;
}

.swiper-android .swiper-slide,
.swiper-wrapper {
  transform: translate3d(0px, 0, 0);
}

.swiper-pointer-events {
  touch-action: pan-y;
}
.swiper-pointer-events.swiper-vertical {
  touch-action: pan-x;
}

.swiper-slide {
  flex-shrink: 0;
  width: 100%;
  height: 100%;
  position: relative;
  transition-property: transform;
}

.swiper-slide-invisible-blank {
  visibility: hidden;
}

/* Auto Height */
.swiper-autoheight,
.swiper-autoheight .swiper-slide {
  height: auto;
}
.swiper-autoheight .swiper-wrapper {
  align-items: flex-start;
  transition-property: transform, height;
}

.swiper-backface-hidden .swiper-slide {
  transform: translateZ(0);
  backface-visibility: hidden;
}

/* 3D Effects */
.swiper-3d, .swiper-3d.swiper-css-mode .swiper-wrapper {
  perspective: 1200px;
}
.swiper-3d .swiper-wrapper,
.swiper-3d .swiper-slide,
.swiper-3d .swiper-slide-shadow,
.swiper-3d .swiper-slide-shadow-left,
.swiper-3d .swiper-slide-shadow-right,
.swiper-3d .swiper-slide-shadow-top,
.swiper-3d .swiper-slide-shadow-bottom,
.swiper-3d .swiper-cube-shadow {
  transform-style: preserve-3d;
}
.swiper-3d .swiper-slide-shadow,
.swiper-3d .swiper-slide-shadow-left,
.swiper-3d .swiper-slide-shadow-right,
.swiper-3d .swiper-slide-shadow-top,
.swiper-3d .swiper-slide-shadow-bottom {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 10;
}
.swiper-3d .swiper-slide-shadow {
  background: rgba(0, 0, 0, 0.15);
}
.swiper-3d .swiper-slide-shadow-left {
  background-image: linear-gradient(to left, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}
.swiper-3d .swiper-slide-shadow-right {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}
.swiper-3d .swiper-slide-shadow-top {
  background-image: linear-gradient(to top, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}
.swiper-3d .swiper-slide-shadow-bottom {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

/* CSS Mode */
.swiper-css-mode > .swiper-wrapper {
  overflow: auto;
  scrollbar-width: none; /* For Firefox */
  -ms-overflow-style: none; /* For Internet Explorer and Edge */
}
.swiper-css-mode > .swiper-wrapper::-webkit-scrollbar {
  display: none;
}
.swiper-css-mode > .swiper-wrapper > .swiper-slide {
  scroll-snap-align: start start;
}

.swiper-horizontal.swiper-css-mode > .swiper-wrapper {
  scroll-snap-type: x mandatory;
}

.swiper-vertical.swiper-css-mode > .swiper-wrapper {
  scroll-snap-type: y mandatory;
}

.swiper-centered > .swiper-wrapper::before {
  content: "";
  flex-shrink: 0;
  order: 9999;
}
.swiper-centered.swiper-horizontal > .swiper-wrapper > .swiper-slide:first-child {
  margin-inline-start: var(--swiper-centered-offset-before);
}
.swiper-centered.swiper-horizontal > .swiper-wrapper::before {
  height: 100%;
  width: var(--swiper-centered-offset-after);
}
.swiper-centered.swiper-vertical > .swiper-wrapper > .swiper-slide:first-child {
  margin-block-start: var(--swiper-centered-offset-before);
}
.swiper-centered.swiper-vertical > .swiper-wrapper::before {
  width: 100%;
  height: var(--swiper-centered-offset-after);
}
.swiper-centered > .swiper-wrapper > .swiper-slide {
  scroll-snap-align: center center;
}

.img-responsive {
  width: 100%;
  height: auto;
}

.page-left-side img {
  min-height: 432px;
}

.page-right-side-wrapper {
  padding-left: 24px;
  transform: translateY(-48%);
}
.page-right-side-wrapper:before {
  content: "";
  position: absolute;
  height: 47%;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #000000 78.65%);
  opacity: 0.5;
  top: 0;
  left: 0;
  width: 100%;
}

h1 {
  position: relative;
  font-family: "ITC Avant Garde Pro Bold";
  font-size: 20px;
  font-weight: 700;
  font-style: normal;
  line-height: 26px;
  margin-bottom: 16px;
  color: #fff;
}

p {
  display: none;
  font-family: "Avenir Book";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 160%;
  color: #666666;
  margin-bottom: 33px;
}

a {
  position: relative;
  font-family: "Avenir Book";
  font-style: normal;
  font-weight: 900;
  font-size: 12.8px;
  line-height: 17px;
  text-transform: capitalize;
  color: #fff;
  text-decoration: none;
  display: inline-block;
  margin-bottom: 6px;
}
a:before {
  content: "";
  position: absolute;
  bottom: -5px;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: #fff;
}

#productSlider {
  padding-top: 32px;
}

.page-right-side-desc {
  max-width: 494px;
}

.product--item-text-desc {
  display: flex;
  justify-content: space-between;
}

.product--item img {
  width: 100%;
}

.product--item-text-title {
  font-family: "Avenir Book";
  font-weight: 400;
  font-size: 12.8px;
  line-height: 20.48px;
}

.product--item-text-price {
  font-family: "Avenir Book";
  font-weight: bold;
  font-size: 12.8px;
  line-height: 20.48px;
}

.product--color-selector {
  display: flex;
}
.product--color-selector input {
  width: 12px;
  height: 12px;
  margin: 0;
  appearance: none;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  border-radius: 50%;
  transition: 0.2s linear;
  cursor: pointer;
  z-index: 3;
}
.product--color-selector label {
  position: relative;
  width: 12px;
  height: 12px;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  border-radius: 50%;
  margin-right: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.2s linear;
}
.product--color-selector label.selected:before {
  position: absolute;
  content: "";
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  border: 1.5px solid #000;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  border-radius: 50%;
}
.product--color-selector label.selected input {
  border: 2px solid #fff;
  width: 10px;
  height: 10px;
}
.product--color-selector .color1 input {
  background-color: #99C4CC;
}
.product--color-selector .color2 input {
  background-color: #CC9999;
}
.product--color-selector .color3 input {
  background-color: #CB99CC;
}
.product--color-selector .color4 input {
  background-color: #A6CC99;
}

.swiper-slide {
  width: 148px;
}

.swiper-button-prev {
  display: none;
  border: 1px solid rgba(0, 0, 0, 0.1);
  position: absolute;
  top: 50%;
  transform: rotate(-180deg) translate(-50%);
  left: 8px;
  z-index: 1;
}

.swiper-button-next {
  right: 8px;
}

.swiper-button-next {
  display: none;
  border: 1px solid rgba(0, 0, 0, 0.1);
  position: absolute;
  top: 50%;
  transform: translate(-50%);
  z-index: 1;
}

.swiper-button-prev.swiper-button-disabled, .swiper-button-next.swiper-button-disabled {
  display: none;
}

@media all and (min-width: 768px) {
  main {
    display: flex;
  }
  .page-left-side, .page-right-side {
    width: 50%;
  }
  .page-right-side-wrapper {
    padding-top: 96px;
    padding-left: 120px;
    transform: translateY(0);
  }
  .page-right-side-wrapper:before {
    display: none;
  }
  h1 {
    font-size: 31.25px;
    line-height: 40.62px;
    margin-bottom: 23px;
    color: #000;
  }
  p {
    display: block;
  }
  .page-left-side img {
    min-height: 100vh;
    object-fit: cover;
  }
  a {
    color: #000;
  }
  a:before {
    background-color: #000;
  }
  #productSlider {
    padding-top: 68px;
  }
  .swiper-slide {
    width: 230px;
  }
  .swiper-button-next, .swiper-button-prev {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    cursor: pointer;
  }
  .swiper-button-next img, .swiper-button-prev img {
    width: 7px;
    height: 13px;
  }
}